import {IProduct} from '../../Interfaces/IProduct';
import {
	CART_RESET,
	CART_ADD,
	CART_REMOVE,
	CART_UPDATE
} from '../actionTypes';


//Cart action creators
export const cartAdd = (product: IProduct) => ({
	type: CART_ADD,
	payload: product
});

export const cartRemove = (id: number) => ({
	type: CART_REMOVE,
	payload: id
});

export const cartReset = () => ({
	type: CART_RESET
});
